*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    background-color: #170C18;
}
.App{
  height: 100vh;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: sans-serif;
  flex-wrap: wrap;
}
.logo-container{
  width: 100%;
  height: 50px;
  margin: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo{
  height: 40px;
}
.calculadora-container{
  width: 400px;
  height: 600px;
  padding: 14px;
  background-color: white;
  border-radius: 20px;
  border: 5px solid blueviolet;
}

.fila{
  margin: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.boton-contenedor{
  height: 83px;
  display: flex;
  flex: 1 1;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 38px;
  color: white;
  background-color: #170C18;
  border-radius: 25px;
  border: 2px solid black;
  cursor: pointer;
  -webkit-user-select: none;
          user-select: none;
  margin: 4px;
}

.boton-contenedor:hover{
  color: #170C18;
  background-color: white;
}
.operador{
  background-color: blueviolet;
  border-color: white;
  border-color: #170C18;
}
.operador:hover{
  background-color: white(235, 152, 0);
  color: blueviolet;
}

.igual{
  background-color: blueviolet;
  border-color: #170C18;
}
.igual:hover{
  color: blueviolet;
  background-color: white;
}
.clear{
  border-color:rgb(114, 5, 5);
  color: white;
  background-color: rgb(209, 15, 15);
}
.clear:hover{
  color: rgb(177, 177, 177);
  background-color: rgb(180, 10, 10);
}
.input{
  height: 75px;
  border-radius: 25px;
  margin-bottom: 20px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-weight: bold;
  font-size: 30px;
  background-color: #170C18;
  color: white;
  padding: 11px 30px 11px 11px;
  border: 2px solid white;
  font-family: monospace;
  letter-spacing: 5px;
}
